.post{
    max-width: 500px;
    background-color: white;
    border:1px solid lightgray;
    margin-bottom: 45px;
}
.post_image{
    width:100%;
    object-fit: contain;
   
  
}
.post_text{
    font-weight: normal;
    padding: 20px;
}
.post_header{
    display: flex;
    align-items: center;
    padding:20px;
}
.post_avatar{
    margin-right: 10px;
}